.composition {
    position: relative;
    &__photo {
        width: 55%;
        box-shadow: $box-shadow;
        border-radius: .5rem;
        position: absolute;
        z-index: 10;
        transition: all .3s;
        outline-offset: 1rem;
        &_n1 {
            top: 2rem;
            left: 8.8rem; }

        &_n2 {
            top: 8.9rem;
            left: 27rem; }

        &_n3 {
            top: 19.4rem;
            left: 11rem; }
        &:hover {
            transform: scale(1.1);
            z-index: 20;
            outline: 1rem solid $color-brown-light; } }

    &:hover &__photo:not(:hover) {
        transform: scale(.95); } }


