.btn {
    &:link, &:visited {
        @include style-link($color-gray);
        padding: 1.5rem 4rem;
        background: $color-white;
        border-radius: 5rem;
        transition: all .5s;
        position: relative;
        font-size: 2rem; }
    &:hover {
        transform: translateY(-0.4rem);
        box-shadow: $box-shadow; }
    &:active {
        background: #cfcfcf;
        transform: translateY(-2px);
        box-shadow: $box-shadow;
        &::after {
            transform: scaleX(1.5) scaleY(1.7);
            opacity: 0; } }
    &::after {
        content: "";
        background: $color-white;
        width: 100%;
        height: 100%;
        border-radius: 5rem;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all .4s; }
    &_animeited {
        animation:  btnToup .5s ease-out 2s;
        animation-fill-mode: backwards; } }
